import React, { useEffect } from "react";
import styles from "../Form.module.scss";
import deleteBtn from "../../../../../assets/images/formPage/delete.svg";

export default function Answer({
  activeCheckbox,
  register,
  subIdx,
  idx,
  arrAnswer,
}) {
  // console.log(idx, subIdx);
  return (
    <label className={styles.label_answer}>
      <div
        className={`${styles.typeInput} ${activeCheckbox ? "" : styles.active}`}
      ></div>
      <input
        // {...register(`question ${idx} answer ${subIdx}`)}
        type="text"
        // name={`question ${idx} answer ${subIdx}`}
        placeholder={` ${subIdx}`}
        required
        onChange={(e) => {
          arrAnswer[idx][subIdx] = e.target?.value;
        }}
      />
      {subIdx !== 0 && (
        <div
          onClick={(e) => {
            e.preventDefault();
            const elem = e.target?.parentElement;
            elem.style.display = "none";
            arrAnswer[idx].splice(subIdx, 1);
            console.log(arrAnswer[idx].length);
            // if (arrAnswer[idx].length === 1) {
            //   arrAnswer[idx].pop();
            //   console.log(22);
            // }
            console.log(arrAnswer);
          }}
          className={styles.deleteBtn_answer}
        >
          <img src={deleteBtn} alt="delete" />
        </div>
      )}
    </label>
  );
}
