import React from "react";
import { useState } from "react";
import styles from "./Form.module.scss";
import Question from "./components/Question";
import { useForm } from "react-hook-form";

export default function Form({ setActivePopup }) {
  const [countQuestions, setCountQuestions] = useState(1);

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const sendForm = (data) => {
    console.log(data);
    setActivePopup(true);
    console.log(arrAnswer);
  };
  const [arrAnswer, setArrAnswer] = useState([[]]);

  return (
    <section>
      <div className={styles.container}>
        <form className={styles.form} onSubmit={handleSubmit(sendForm)}>
          <label className={styles.label_nameForm}>
            
            <input
              {...register("nameForm")}
              type="text"
              name="nameForm"
              placeholder=" "
              required
            />
          </label>
          {[...Array(countQuestions)].map((item, idx) => (
            <Question
              key={`question item - ${idx}`}
              countQuestions={countQuestions}
              register={register}
              idx={idx}
              arrAnswer={arrAnswer}
            />
          ))}
          <button
            type="button"
            onClick={() => {
              setArrAnswer([...arrAnswer, []]);
              console.log(arrAnswer);
              setCountQuestions(countQuestions + 1);
            }}
            className={styles.btn}
          >
             
          </button>
          <button type="submit" className={styles.btn}>
            
          </button>
        </form>
      </div>
    </section>
  );
}
