import React from "react";
import styles from "./Popup.module.scss";
import exitBtn from "../../assets/images/popup/exit-btn.svg";
import { useClipboard } from "use-clipboard-copy";

export default function Popup({ activePopup, setActivePopup, pollLink }) {
  const copy = useClipboard();
  const link = `https://localhost:3000/${pollLink}`;
  return (
    <div
      className={
        activePopup ? `${styles.popup} ${styles.active}` : styles.popup
      }
      onClick={() => setActivePopup(false)}
    >
      <div
        className={
          activePopup ? `${styles.content} ${styles.active}` : styles.content
        }
        onClick={(e) => e.stopPropagation()}
      >
        <div className={styles.title}>
          <h2></h2>
          <img onClick={() => setActivePopup(false)} src={exitBtn} alt="exit" />
        </div>
        <p className={styles.paragraph}></p>
        <p ref={copy.target} value={link} className={styles.link}>
          {link}
        </p>
        <div className={styles.btns}>
          <button onClick={() => setActivePopup(false)} type="button">
            
          </button>

          <button onClick={copy.copy(link)} type="button">
            
          </button>
        </div>
      </div>
    </div>
  );
}
