import React from "react";
import { Link, useNavigate } from "react-router-dom";
import styles from "./Registration.module.scss";
import { useForm } from "react-hook-form";
import axios from "axios";

export default function Registration({ setLogin, setEmail }) {
  //   const data = {
  //     email: "chuvsu@form.ru",
  //     password: "qwerty",
  //   };

  const navigate = useNavigate();

  const sendReg = ({ name, surname, email, password }) => {
    if (name !== "" && surname !== "" && email !== "" && password !== "") {
      console.log(name, surname, email, password);
      // axios
      //   .get(
      //     `http://localhost:5000/api/v1/requser?name=${name}&familya=${surname}&email=${email}&password=${password}`
      //   )
      //   .then(({ data }) => {
      //     console.log(data.response.flag);
      //     if (data.response.flag) {
      //       setLogin(true);
      //       setEmail(email);
      //       navigate("/main-page");
      //     }
      //   });
      setLogin(true);
      navigate("/hakaton/main-page");
    }
  };

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  return (
    <section className={styles.login}>
      <div className={styles.wrapper}>
        <div className={styles.content}>
          <div className={styles.text}>
            <h2>CHUVSU</h2>
            <h1>FORM</h1>
            <p className={styles.subtitle}>. . .</p>
          </div>
          <form className={styles.form} onSubmit={handleSubmit(sendReg)}>
            <h3></h3>
            <div className={styles.form__wrapper}>
              <label>
                
                <input
                  {...register("name")}
                  type="text"
                  name="name"
                  placeholder=""
                  required
                />
              </label>
              <label>
                
                <input
                  {...register("surname")}
                  type="text"
                  name="surname"
                  placeholder=""
                  required
                />
              </label>
            </div>
            <label>
              
              <input
                {...register("email")}
                type="email"
                name="email"
                placeholder="Chuvsu@form.com"
                required
              />
            </label>
            <label>
              
              <input
                {...register("password")}
                type="password"
                name="password"
                placeholder="qwerty"
                required
              />
            </label>
            <button type="submit" className={styles.btn}>
              
            </button>
          </form>
        </div>
      </div>
    </section>
  );
}
