import React, { useEffect } from "react";
import axios from "axios";
import Header from "../Header/Header";
import CreateForm from "./components/CreateForm/CreateForm";
import { useNavigate } from "react-router-dom";
import Recent from "./components/Recent/Recent";

export default function MainPage({ login, emailUser, setPollLink }) {
  const navigate = useNavigate();
  // axios
  //   .get(
  //     "http://localhost:5000/api/v1/requser?name= test&familya=test&email=123@bk.ru&password=qwe&form=[]&form_admin=[]"
  //   )
  //   .then((data) => console.log(data));
  useEffect(() => {
    !login && navigate("/hakaton/login");
  }, []);
  return (
    <>
      {login && (
        <>
          <Header />
          <div className="container">
            <CreateForm emailUser={emailUser} setPollLink={setPollLink} />
            <Recent />
          </div>
        </>
      )}
    </>
  );
}
