import "./App.scss";
import { Routes, Route } from "react-router-dom";
import MainPage from "./components/mainPage/MainPage";
import Login from "./components/Login/Login";
import { useState } from "react";
import FormPage from "./components/FormPage/FormPage";
import Registration from "./components/Registration/Registration";
import Popup from "./components/Popup/Popup";
import PollPage from "./components/PollPage/PollPage";

function App() {
  const [login, setLogin] = useState(false);
  const [activePopup, setActivePopup] = useState(false);
  const [emailUser, setEmail] = useState("");
  const [pollLink, setPollLink] = useState("");
  console.log(pollLink);

  return (
    <div className="App">
      <Routes>
        <Route path="/hakaton/" element={<Login setLogin={setLogin} />} />
        <Route
          path="/hakaton//login"
          element={<Login setLogin={setLogin} setEmail={setEmail} />}
        />
        <Route
          path="/hakaton/registration"
          element={<Registration setLogin={setLogin} setEmail={setEmail} />}
        />
        <Route
          path="/hakaton/main-page"
          element={
            <MainPage
              login={login}
              emailUser={emailUser}
              setPollLink={setPollLink}
            />
          }
        />
        <Route
          path="/hakaton/create-form"
          element={<FormPage login={login} setActivePopup={setActivePopup} />}
        />
        <Route path="/hakaton/example" element={<PollPage />} />
      </Routes>
      <Popup
        activePopup={activePopup}
        setActivePopup={setActivePopup}
        pollLink={pollLink}
      />
    </div>
  );
}

export default App;
