import React, { useState } from "react";
import styles from "../Form.module.scss";
import plus from "../../../../../assets/images/createForm/plus.svg";
import Answer from "./Answer";
import deleteBtn from "../../../../../assets/images/formPage/delete.svg";

export default function Question({ countQuestions, register, idx, arrAnswer }) {
  const [countAnswers, setCountAnswers] = useState(1);
  const [activeCheckbox, setActiveCheckbox] = useState(true);

  return (
    <div
      className={`${styles.question} ${
        countQuestions === 1 ? styles["margin-bottom"] : ""
      }`}
    >
      <label className={styles.label_question}>
        <input
          {...register(`nameQuestion${idx}`)}
          type="text"
          name={`nameQuestion${idx}`}
          placeholder=""
          required
        />
        {idx !== 0 && (
          <div
            onClick={(e) => {
              e.preventDefault();
              const elem = e.target?.parentElement?.parentElement;
              elem.style.display = "none";
            }}
            className={styles.deleteBtn_question}
          >
            <img src={deleteBtn} alt="delete" />
          </div>
        )}
      </label>
      <div className={styles.wrapper}>
        <label
          className={!activeCheckbox ? styles.active : ""}
          onClick={() => {
            setActiveCheckbox(false);
          }}
        >
            
          <input
            {...register(`question${idx}_type`)}
            type="radio"
            name={`question${idx}_type`}
            value="radio"
            required
          />
        </label>
        <label
          className={activeCheckbox ? styles.active : ""}
          onClick={() => setActiveCheckbox(true)}
        >
            
          <input
            {...register(`question${idx}_type`)}
            type="radio"
            name={`question${idx}_type`}
            value="checkbox"
            defaultChecked
            required
          />
        </label>
      </div>
      {[...Array(countAnswers)].map((item, subIdx) => (
        <Answer
          key={`answer item - ${subIdx}`}
          activeCheckbox={activeCheckbox}
          register={register}
          subIdx={subIdx}
          idx={idx}
          arrAnswer={arrAnswer}
        />
      ))}
      <div
        className={styles.plus}
        onClick={() => setCountAnswers(countAnswers + 1)}
      >
        <img src={plus} alt="plus" />
      </div>
    </div>
  );
}
