import React from "react";
import { Link, useNavigate } from "react-router-dom";
import styles from "./Login.module.scss";
import { useForm } from "react-hook-form";
import axios from "axios";

export default function Login({ setLogin, setEmail }) {
  const data = {
    email: "chuvsu@form.ru",
    password: "qwerty",
  };

  const navigate = useNavigate();

  const sendLogin = ({ email, password }) => {
    if (email === data.email && password === data.password) {
      console.log("LOGIN SUCCESSFUL");
      setLogin(true);
      navigate("/hakaton/main-page");
    }
    // axios
    //   .get(
    //     `http://localhost:5000/api/v1/login?email="${email}"&password=${password}`
    //   )
    //   .then(({ data }) => {
    //     console.log(data.response.status);
    //     if (data.response.status) {
    //       console.log("LOGIN SUCCESSFUL");
    //       setLogin(true);
    //       setEmail(email);
    //       navigate("/hakaton//main-page");
    //     }
    //   });
  };

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  return (
    <section className={styles.login}>
      <div className={styles.wrapper}>
        <div className={styles.content}>
          <div className={styles.text}>
            <h2>CHUVSU</h2>
            <h1>FORM</h1>
            <p className={styles.subtitle}>. . .</p>
          </div>
          <form className={styles.form} onSubmit={handleSubmit(sendLogin)}>
            <h3></h3>
            <label>
              
              <input
                {...register("email")}
                type="email"
                name="email"
                placeholder="Chuvsu@form.com"
                required
              />
            </label>
            <label>
              
              <input
                {...register("password")}
                type="password"
                name="password"
                placeholder="qwerty"
                required
              />
            </label>
            <Link to={"/hakaton/registration"}>
              <p className={styles["create-acc"]}> </p>
            </Link>
            <button type="submit" className={styles.btn}>
              
            </button>
          </form>
        </div>
      </div>
    </section>
  );
}
